import React, { useState, useEffect } from 'react';


import logo from './logo.svg';
import './App.css';


// api
import WagtailPages from './components/WagtailPages';
import Posts from './components/Posts';

// 进度条
import Circle from 'react-circle';
import ProgressBar from 'react-bootstrap/ProgressBar';
import 'bootstrap/dist/css/bootstrap.min.css';

// 时钟
import Clock from './components/Clock.js';

import  { StrictMode } from 'react';//提交表单
import { sculptureList } from './components/data.js';//看图片
import ColorSwitch from './components/ColorSwitch.js';//改变颜色

// 启示文本
import FancyText from './启示文本/FancyText';
import InspirationGenerator from './启示文本/InspirationGenerator';
import Copyright from './启示文本/Copyright';

function useTime() {
  const [time, setTime] = useState(() => new Date());
  useEffect(() => {
    const id = setInterval(() => {
      setTime(new Date());
    }, 1000);
    return () => clearInterval(id);
  }, []);
  return time;
}
// 提交表单
let SignUp =function Signup() {
  return (
    <form onSubmit={e => {
      e.preventDefault();
      alert('提交表单！');
    }}>
      <input />
      <button>发送</button>
    </form>
  );
}
// 事件传播
let ToolBar = function Toolbar() {
  return (
    <div className="Toolbar" onClick={() => {
      alert('你点击了 toolbar ！');
    }}>
      <button onClick={() => alert('正在播放！')}>
        播放电影
      </button>
      <button onClick={() => alert('正在上传！')}>
        事件传播
      </button>
    </div>
  );
}



function App() {
  // 进度条
    const [now, setScore] = useState(0);
    function increment() {setScore(s => s + 1);}
// 颜色
const time = useTime();
const [color, setColor] = useState('lightcoral');


// 看图片
  const [index, setIndex] = useState(0);
  const [showMore, setShowMore] = useState(false);
  const hasNext = index < sculptureList.length - 1;
  function handleNextClick() {
    if (hasNext) {
      setIndex(index + 1);
    } else {
      setIndex(0);
    }
  }
  function handleMoreClick() {
    setShowMore(!showMore);
  }
  let sculpture = sculptureList[index];

// 改变颜色
  const [clicks, setClicks] = useState(0);
  function handleClickOutside() {
    setClicks(c => c + 1);
  }
  function getRandomLightColor() {
    let r = 150 + Math.round(100 * Math.random());
    let g = 150 + Math.round(100 * Math.random());
    let b = 150 + Math.round(100 * Math.random());
    return `rgb(${r}, ${g}, ${b})`;
  }
  function handleChangeColor() {
    let bodyStyle = document.body.style;
    bodyStyle.backgroundColor = getRandomLightColor();
  }



  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
<div onClick={() => increment()} style={{ width: '200px', height: '200px' }}> 
<Circle
  animate={true} 
  animationDuration="1s" 
  responsive={true} 
  // size={150}
  lineWidth={30} 
  progressColor="Maroon" //cornflowerblue
  bgColor="Moccasin"//whitesmoke
  textColor="hotpink" 
  textStyle={{
    font: 'bold 5rem Helvetica, Arial, sans-serif' 
  }}
  // percentSpacing={10} 
  roundedStroke={true}
  showPercentage={true} 
  showPercentageSymbol={true} 
  progress={now}
/>
</div>
                <div onClick={() => increment()}>
                    <p>
                      Edit <code>src/App.tsx</code> and save to reload.
                      +1
                      
                    </p>
                    <ProgressBar now={now} label={`${now}%`} />
                </div> 


      <button onClick={() => {
        increment();
        increment();
        increment();
      }}>+3</button>

        <a className="App-link" href="https://reactjs.org" target="_blank" rel="noopener noreferrer">
          Learn React
        </a>


<WagtailPages />
<Posts />


        <p>
          选择一个颜色:{' '}
          <select value={color} onChange={e => setColor(e.target.value)}>
            <option value="lightcoral">浅珊瑚色</option>
            <option value="midnightblue">午夜蓝</option>
            <option value="rebeccapurple">丽贝卡紫</option>
          </select>
        </p>
        <Clock color={color} time={time.toLocaleTimeString()} /> 

      </header>
<header>
{/*提交表单*/}
  <StrictMode>
    <SignUp />
    <ToolBar />
  </StrictMode>
{/*看图片*/}
      <button onClick={handleNextClick}>
        Next
      </button>
      <h2>
        <i>{sculpture.name} </i>
        by {sculpture.artist}
      </h2>
      <h3>
        ({index + 1} of {sculptureList.length})
      </h3>
      <button onClick={handleMoreClick}>
        {showMore ? 'Hide' : 'Show'} details
      </button>
      {showMore && <p>{sculpture.description}</p>}
      <img
        src={sculpture.url}
        alt={sculpture.alt}
      />


    <div style={{ width: '100%', height: '100%' }} onClick={handleClickOutside}>
      <ColorSwitch onChangeColor={handleChangeColor} />
      <br />
      <br />
      <h2>页面点击次数：{clicks}</h2>
    </div>
{/*启示文本*/}
      <FancyText title text="Get Inspired App" />
      <InspirationGenerator>
        <Copyright year={2004} />
      </InspirationGenerator>
 </header>
    </div>
  );
}

export default App;
